<template>
    <div id="text-area">
        <div id="terminal-bar">
            <div id="terminal-title">RT Terminal</div>
        </div>
        <textarea 
            id="terminal" 
            @keydown.enter="startNewLine" 
            @keydown.up="makeCursorInRange_keyboard"
            @keydown.left="makeCursorInRange_keyboard"
            @keydown.backspace="makeCursorInRange_backspace"
            @keypress.enter="startNewLine" 
            @keypress.up="makeCursorInRange_keyboard"
            @keypress.left="makeCursorInRange_keyboard"
            @keypress.backspace="makeCursorInRange_backspace"
            @click="makeCursorInRange"
            @mousedown="makeCursorInRange"
            spellcheck="false">
        </textarea>
    </div>
</template>

<script>
import axios from 'axios';
export default{
    name:'TerminalContainer',
    props:{
        point:String
    },
    methods:{
        initTerminal(){
            //initialize the text
            const tA = document.getElementById('terminal');
            tA.value = "Copyright (c) 2023, Bruce Chen, brucechen@whu.edu.cn\nWelcome to RT Terminal v0.0.1\n"+
            "  _____ _______   _______                  _             _ \n"+
            " |  __ \\__   __| |__   __|                (_)           | |\n"+
            " | |__) | | |       | | ___ _ __ _ __ ___  _ _ __   __ _| |\n"+
            " |  _  /  | |       | |/ _ \\ '__| '_ ` _ \\| | '_ \\ / _` | |\n"+
            " | | \\ \\  | |       | |  __/ |  | | | | | | | | | | (_| | |\n"+
            " |_|  \\_\\ |_|       |_|\\___|_|  |_| |_| |_|_|_| |_|\\__,_|_|\n"+
            "\nroot:\\> ";
        },
    },
    setup(props,{emit}){
        console.log(props);
        let lineEndPos = 452;
        function setLineEnd(tA){
            lineEndPos = tA.value.length;
        }

        const startNewLine = (event) => {
            event.preventDefault();//屏蔽原本的换行输入
            const tA = document.getElementById('terminal');
            //TODO:process input string
            // let strIn = tA.value.substring(lineEndPos);
            axios.post('/api/inputString',{
                inputString : tA.value.substring(lineEndPos),
            }).then((res)=>{
                //console.log(res);
                //const queryRes = res.data;
                tA.value += '\n';
                tA.value += res.data.logInfo;
                emit('doQuery',res.data);
                tA.value += '\nroot:\\> ';
                setLineEnd(tA);
                tA.scrollTop = tA.scrollHeight;
            }).catch((err)=>{
                console.log(err);
            });
            //tA.value += '\nroot:\\> ';
            
        }

        const makeCursorInRange = (event) => {
            const tA = document.getElementById('terminal');
            let cursorPos = tA.selectionStart;
            //console.log(cursorPos);
            if(cursorPos < lineEndPos){
                event.preventDefault();
                tA.setSelectionRange(lineEndPos,lineEndPos);
            }
        }

        const makeCursorInRange_keyboard = (event) => {//keyboard事件不可取消，所以把边界条件设置的更严格从而可以提前检验
            const tA = document.getElementById('terminal');
            let cursorPos = tA.selectionStart;
            //console.log(cursorPos);
            if(cursorPos < lineEndPos + 1){
                event.preventDefault();
                tA.setSelectionRange(lineEndPos,lineEndPos);
            }
        }

        const makeCursorInRange_backspace = (event) => {
            const tA = document.getElementById('terminal');
            let cursorPos = tA.selectionStart;
            if(cursorPos <= lineEndPos){
                event.preventDefault();
                //tA.setSelectionRange(lineEndPos,lineEndPos);
            }
        }

        return {
            startNewLine,
            makeCursorInRange,
            makeCursorInRange_keyboard,
            makeCursorInRange_backspace,
        }
    },
    mounted(){
        this.initTerminal();
    }
}
</script>
<style scoped>
    #text-area{
        right: 0px;
        bottom: 0px;
        width: 700px;
        height: 40%;
        position: absolute;
        z-index: 1;
    }
    #terminal{
        font-family: Consolas, monaco, monospace;
        font-size: 20px; 
        font-style: normal; 
        font-variant: normal; 
        line-height: 20px;
        opacity: 0.8;
        color:whitesmoke;
        background-color: black;
        width: 100%;
        height: 92%;
        resize: none;
    }
    #terminal-bar{
        background-color: rgb(187, 187, 187);
        border-top-left-radius: 10px;
        height: 8%;
    }
    #terminal-title{
        font-family: Consolas, monaco, monospace;
        font-size: 20px; 
        font-style: normal; 
        font-variant: normal; 
    }
</style>