<template>
    <div id="container">
        <TerminalContainer :point="newPoint" @doQuery="modifyMap"></TerminalContainer>
    </div>
</template>
<script>
import AMapLoader from '@amap/amap-jsapi-loader';
import { shallowRef } from '@vue/reactivity';
import TerminalContainer from './Terminal.vue';
export default{
    name:'MapContainer',
    components:{
        TerminalContainer,
    },
    methods:{
        initMap(){
            AMapLoader.load({
                key:"27d5c43e75cb92aaadb2da2fe05dcba6",             // 申请好的Web端开发者Key，首次调用 load 时必填
                version:"2.0",      // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                plugins:[''],       // 需要使用的的插件列表，如比例尺'AMap.Scale'等
            }).then((AMap)=>{
                this.map = new AMap.Map("container",{  //设置地图容器id
                    viewMode:"3D",    //是否为3D地图模式
                    zoom:13,           //初始化地图级别
                    center:[116.40,39.90], //初始化地图中心点位置
                });
                this.map.on('click',function(e){
                    console.log('(' + e.lnglat.getLng() + ',' + e.lnglat.getLat() + ')');
                });
            }).catch(e=>{
                console.log(e);
            })
        },
    },
    setup() {
        const map = shallowRef(null);
        const modifyMap = (res) => {
            //console.log(res);
            AMapLoader.load({
                key:"27d5c43e75cb92aaadb2da2fe05dcba6",             // 申请好的Web端开发者Key，首次调用 load 时必填
                version:"2.0",      // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                plugins:[''],       // 需要使用的的插件列表，如比例尺'AMap.Scale'等
            }).then((AMap)=>{
                //console.log('AMap is ',AMap);
                const primitivesArray = res.retArray;
                //console.log('primitives length is ',primitives.length);
                console.log(primitivesArray);
                if(primitivesArray.length > 0){
                    //console.log('resArray is',res.retArray);
                    for(var j = 0; j < primitivesArray.length; ++j){
                        const primitives = primitivesArray[j];
                        var path = [];
                        for(var i = 0; i < primitives.length; ++i){
                            var x = primitives[i][0];
                            var y = primitives[i][1];
                            var tpoint = new AMap.LngLat(x,y);
                            path.push(tpoint);
                        }
                        //console.log('path is ',path);
                        if(res.retType == 'trajectory' || res.retType == 'trajectory-to-be-queried'){
                            var polyline = new AMap.Polyline({
                                path: path,  
                                borderWeight: 2, // 线条宽度，默认为 1
                                strokeColor: 'blue', // 线条颜色
                                lineJoin: 'round' // 折线拐点连接处样式
                            });
                            map.value.add(polyline);
                        }
                        else if(res.retType == 'polygon'){
                            var polygon = new AMap.Polygon({
                                path: path,  
                                fillColor: '#fff', // 多边形填充颜色
                                borderWeight: 2, // 线条宽度，默认为 1
                                strokeColor: 'red', // 线条颜色
                            });
                            map.value.add(polygon);
                        }
                    }
                }
            }).catch(e=>{
                console.log(e);
            })
        };
        
        return{
            map,
            modifyMap,
        }
    },
    mounted(){
        //DOM初始化完成进行地图初始化
        this.initMap();
    }
}


</script>

<style  scoped>
#container{
    padding:0px;
    margin: 0px;
    width: 100%;
    height: 800px;
}
</style>